<template>
  <div class="posts">
    <ul :data="posts">
      <li>
        <div class="post" v-for="(item,index) in posts" :key="item.id">
          <Table :item="item" :is-detail="false">
          </Table>
        </div>
      </li>
    </ul>
    <div class="block">
      <el-pagination
        :page-size="page_size"
        :pager-count="5"
        @current-change=TableListChange
        :current-page="page_index"
        :layout="page_layout"
        :total=page_total>
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //
      isMobile: null,
      type: 1,
      // 
      page_index: 1,
      // 
      page_size: 10,
      // 
      page_total: 0,
      // 
      page_layout: '',
      // 
      posts: {},
      genders: '',
      LoadingStatus: false
    }
  },
  created() {
    //
    this.isMobile = this.getIsMobile()

    this.getTableList()
    this.getTableTotal()
  },
  watch: {
    isMobile(newValue, oldValue) {
      //
      if (newValue) {
        this.page_layout = 'prev, pager, next'
      } else {
        this.page_layout = 'prev, pager, next, jumper'
      }
    }
  },
  methods: {
    async getTableList() {
      await this.$http.get('/api/table/pageList', {
        pageIndex: this.page_index,
        pageSize: this.page_size
      }).then((data) => {
        this.posts = data.data
      })
    },

    // 
    async getTableTotal() {
      await this.$http.get('/api/table/count').then((data) => {
        this.page_total = data.data
      })
    },
    // 
    TableListChange(newpage) {
      this.page_index = newpage
      this.getTableList()
    }
  }
}
</script>

